@import "./utils/_media-variables.scss";

// laptop 
@media screen and (min-width: $tablet) and (max-width: $laptop){
    header{
        border-bottom: 1px solid var(--light-gray);

        .wrapper{
            display: flex;
            align-items: center;
            justify-content: space-between;
            max-width: 1266px;
            padding: 10px 10px;
            position: relative;

            &:last-child{
                padding: 20px 5px;
            }
        }
    }

    input{
        &[type="text"],
        &[type="password"],
        &[type="tel"],
        &[type="email"]{
            padding-top: 11px;
            padding-bottom: 11px;
        } 
    }

    #header-location, .header-phone, 
    .header_nav-item a,.burger-button,
    .header_button-set a, .header_button-set button,
    .header-middle-list a, .header-logo{
        padding: 10px 5px;
        display: block;
    }

    #header-location, .header-phone, .burger-button{
        display: flex;
        align-items: center;
        gap: 6px;

        p, span{
            padding-top: 1px;
        }
    }

    #header-location, .header-phone, .burger-button,.header_button-set{
        svg{
            width: 15px;
            height: 15px;
        }
    }

    .header_nav{
        max-width: 790px;
        width: 100%;
    }

    .header_nav-list{
        display: flex;
        justify-content: space-between;
        width: 100%;
    }

    .header_button-set{
        display: flex;
        max-width: 155px;
        width: 100%;
        justify-content: space-between;

        svg{
            display: block;
        }
    }

    .header_middle{
        width: 100%;
        padding-left: 14px;
    }

    .header-middle_search{
        display: none;
        position: relative;
        padding-right: 30px;
        padding-left: 70px;
    }

    .header-middle-list{
        display: flex;
        gap: 14px;
    }

    .header-logo{
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        
        img{
            height: 32px;
            width: auto;
        }
    }

    input.header_search-bar{
        border-bottom: 1px solid var(--light-gray);
        padding-left: 5px;
        padding-right: 5px;
        font:var(--body-2);
    }

    #header_search-bar-close{
        position: absolute;
        right: 30px;
        top: 50%;
        transform: translateY(-50%);
        

        svg{
            display: block;
            width: 15px;
            height: 15px;

            &:hover{
                path{
                    fill: var(--gray);
                }
            }
        }
    }

    .search-block{
        background: white;
        border-bottom: 1px solid var(--light-gray);
        padding-top: 35px;
        padding-bottom: 57px;
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: calc(100vh - var(--header-height));
        overflow: auto;

        h2{
            margin-bottom: 20px;
        }

        .wrapper{
            max-width: 925px;
        }

        li{
            margin-top: 10px;
        }
    }

    .search-block__results{
        margin-bottom: 30px;
    }

    // Auth
    #authorization{
        position: absolute;
        width: 100%;
        height: calc(100vh - var(--header-height));
        bottom: 0;
        left: 0;
        display: none;

        &.active{
            display: block;
        }

        .wrapper{
            height: 100%;
            padding: 70px 20px;
            display: flex;
            justify-content: center;
            overflow: auto;
        }
    }

    .authorization-container{
        max-width: 560px;
        width: 100%;
        height: fit-content;
        background: var(--white);
        padding: 80px;
        padding-bottom: 100px;
        position: relative;

        h2{
            text-align: center;
            margin-bottom: 60px;
        }

        input[type="text"]{
            margin-bottom: 37px;
        }

        form{
            display: flex;
            flex-direction: column;
            height: fit-content;
        }

        input[type="submit"]{
            padding: 20px;
            max-width: 270px;
            width: 100%;
            margin: 0 auto;
        }
    }

    #authorization-close{
        position: absolute;
        right: 20px;
        top: 25px;
        padding: 5px;
    }

    .password-input{
        position: relative;
    }

    .show-pass{
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        padding: 5px;
        padding-right: 0;
        display: block;

        svg{
            display: block;
        }

        &.active{
            svg{
                path{
                    fill: var(--black);
                }
            }
        }
    }

    .authorization__pass-forgot{
        text-align: right;
        display: inline-block;
        margin-top: 13px;
        margin-bottom: 37px;
    }

    // Cart
    #cart{
        position: absolute;
        width: 100%;
        height: calc(100vh - var(--header-height));
        bottom: 0;
        left: 0;
        display: none;

        &.active{
            display: block;
        }

        .wrapper{
            width: 100%;
            overflow: auto;
        }
    }

    .cart-block{
        max-width: 720px;
        height: 100%;
        margin-left: auto;
        width: 100%;
        background: var(--white);
        display: flex;
        flex-direction: column;
        height: calc(100vh - var(--header-height));
    }

    .cart-main{
        padding: 55px 80px 24px 80px;
        display: flex;
        flex-direction: column;
        height: calc(100% - 100px);
    }

    .cart-header{
        display: flex;
        gap: 15px;
        justify-content: space-between;
        padding-bottom: 19px;
        border-bottom: 1px solid var(--light-gray);
    }

    #cart-close, .cart-item-remove{
        padding: 5px;
        margin-right: -5px;
        display: block;

        svg{
            display: block;
        }
    }

    .cart-lineitem{
        height: 100%;
        overflow: auto;
        flex-direction: column;
    }

    .cart-item{
        margin-top: 25px;
        overflow: hidden;
        padding-bottom: 15px;
        border-bottom: 1px solid var(--light-gray);
        width: 100%;
        height: fit-content;
    }

    .cart-item__main{
        display: grid;
        grid-template-columns: min-content 1fr;
        grid-template-rows: min-content 1fr;
        column-gap: 40px;
        margin-bottom: 20px;

        h2{
            max-width: 310px;
        }
    }

    .cart-item__thumb{
        grid-column: 1/2;
        grid-row: 1/3;
        width: 120px;
        height: 120px;
        object-fit: cover;
    }

    .cart-item__header{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 12px;
    }

    .cart-item-remove{
        display: block;
        height: fit-content;
    }

    .cart-item__footer{
        display: flex;
        justify-content: space-between;
    }

    .cart-item__amount{
        display: flex;
        align-items: flex-end;
        gap: 50px;

        select{
            min-width: 64px;
            padding: 5px;
            border: 0;
            border-bottom: 1px solid var(--light-gray);
            color: var(--black);
            font: var(--h4);
            padding-left: 15px;
            -webkit-appearance: none;
            -moz-appearance: none;
            position: relative;
            background: transparent url("../img/select-arrow.svg") no-repeat right 4px center;
            background-position-y: 16px;
        }
    }

    .cart-summary{
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
    }

    .cart-footer{
        display: grid;
        grid-template-columns: calc( calc(400% * 100) / 720 ) 1fr;
        border-top: 1px solid var(--light-gray);

        a,button{
            padding: 36px;
        }
    }

    .cart-empty-footer{
        a{
            padding: 36px;
            display: block;
        }
    }

    .cart-empty{
        padding-top: 60px;

        h3{
            margin-bottom: 26px;
        }
    }


    // Favorites
    #favorites{
        position: absolute;
        width: 100%;
        height: calc(100vh - var(--header-height));
        bottom: 0;
        left: 0;
        // display: none;

        &.active{
            display: block;
        }

        .wrapper{
            width: 100%;
            overflow: auto;
        }
    }

    .favorites-block{
        max-width: 720px;
        height: 100%;
        margin-left: auto;
        width: 100%;
        background: var(--white);
        display: flex;
        flex-direction: column;
        height: calc(100vh - var(--header-height));
    }

    .favorites-main{
        padding: 55px 80px 24px 80px;
        display: flex;
        flex-direction: column;
        height: calc(100% - 100px);
    }

    .favorites-header{
        display: flex;
        gap: 15px;
        justify-content: space-between;
        padding-bottom: 19px;
        border-bottom: 1px solid var(--light-gray);
    }

    #favorites-close, .favorites-item-remove{
        padding: 5px;
        margin-right: -5px;
        display: block;

        svg{
            display: block;
        }
    }

    .favorites-lineitem{
        height: 100%;
        overflow: auto;
        flex-direction: column;
    }

    .favorites-item{
        margin-top: 25px;
        overflow: hidden;
        padding-bottom: 15px;
        border-bottom: 1px solid var(--light-gray);
        width: 100%;
        height: fit-content;
    }

    .favorites-item__main{
        display: grid;
        grid-template-columns: min-content 1fr;
        grid-template-rows: min-content 1fr;
        column-gap: 40px;
        margin-bottom: 20px;

        h2{
            max-width: 310px;
        }
    }

    .favorites-item__thumb{
        grid-column: 1/2;
        grid-row: 1/3;
        width: 120px;
        height: 120px;
        object-fit: cover;
    }

    .favorites-item__header{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 12px;
    }

    .favorites-item-remove{
        display: block;
        height: fit-content;
    }

    .favorites-item__footer{
        display: flex;
        justify-content: space-between;
    }

    .favorites-item__amount{
        display: flex;
        align-items: flex-end;
        gap: 50px;

        select{
            min-width: 64px;
            padding: 5px;
            border: 0;
            border-bottom: 1px solid var(--light-gray);
            color: var(--black);
            font: var(--h4);
            padding-left: 15px;
            -webkit-appearance: none;
            -moz-appearance: none;
            position: relative;
            background: transparent url("../img/select-arrow.svg") no-repeat right 4px center;
            background-position-y: 16px;
        }
    }

    .favorites-summary{
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
    }

    .favorites-footer{
        button{
            padding: 37px;
            display: block;
            width: 100%;
        }
    }


    .favorites-empty{
        padding-top: 60px;

        h3{
            margin-bottom: 26px;
        }
    }










    main {
        .news {
            .discount {
                max-width: 1600px;
                margin: 0 auto;
                padding: 200px 50px 50px;

                .sub-title {
                    color: var(--light-gray);
                    padding: 0 0 15px;
                }
            }

            .discount-2 {
                display: grid;
                grid-template-columns: 33% 67%;

                .block_left {
                    border-right: 1px solid var(--light-gray);
                }

                .block_right {
                    width: 100%;
                    max-width: 1050px;
                    margin: 0 auto;
                    padding: 0 40px;

                    .block_right-wrapper {
                        max-width: 543px;
                        padding: 88px 0 156px;
                    }
                }
            }
        }
    }

    .news-article_intro{
        padding: 142px 116px 35px 116px;

        p{
            padding-bottom: 11px;
        }
    }

    .news-article_main{
        display: grid;
        grid-template-columns: calc(640% * 100 / 1920) calc(1280% * 100 / 1920);

        &>div{
            border-bottom: 1px solid var(--light-gray);

            &:nth-child(2n){
                border-left: 1px solid var(--light-gray);
            }
        }
    }

    .news_main-intro{
        padding: 63px 63px 107px 63px;

        p{
            max-width: 393px;
        }
    }

}